import React, { useEffect, useState } from "react";
import { useTranslation } from "react-i18next";
import i18n from "i18n/i18n";
import queryString from "query-string";
import { withFormik } from "formik";
import * as Yup from "yup";
import { useHistory } from "react-router-dom";
import { toast } from "react-toastify";
import "./TypeTeachingPlanEdit.css";

import Breadcrumbs from "components/admin/Breadcrumbs/Breadcrumbs";
import PageHeader from "./components/PageHeader/PageHeader";
import typeTeachingPlanApi from "api/common/typeTeachingPlan";
import {
  FaExchangeAlt,
  FaPlus,
  FaSync,
  FaTextWidth,
  FaTrash,
  FaUserAlt,
  FaUsersCog,
} from "react-icons/fa";
import {
  Button,
  Form,
  FormGroup,
  FormText,
  Input,
  InputGroup,
  InputGroupAddon,
  InputGroupText,
  Label,
} from "reactstrap";
import AsteriskIcon from "components/common/AsteriskIcon/AsteriskIcon";
import Notification from "components/common/Notification/Notification";
import LoadingBar from "components/common/LoadingBar/LoadingBar";
import { RoutesString } from "pages/routesString";

const initValues = {
  ma: "",
  ten: "",
};

const TypeTeachingPlanEdit = function ({
  values,
  handleChange,
  resetForm,
  errors,
}) {
  const { t } = useTranslation();

  const history = useHistory();

  const [id, setId] = useState(null);
  const [isUpdatePage, setIsUpdatePage] = useState(false);
  const [notification, setNotification] = useState({
    color: "danger",
    isOpen: false,
    messages: [],
  });
  const [isLoading, setIsLoading] = useState(false);

  const availableStatus = [
    {
      value: true,
      name: t("admin.common.DELETED"),
    },
    {
      value: false,
      name: t("admin.common.AVAILABLE"),
    },
  ];

  useEffect(async () => {
    const parsed = queryString.parse(window.location.search);
    if (parsed.id) {
      setId(parsed.id);
      setIsUpdatePage(true);
      updateForm(parsed.id);
    } else {
      values.ma = "";
      values.ten= "";
    }
  }, []);

  const updateForm = async function (id) {
    setIsLoading(true);
    try {
      const res = await typeTeachingPlanApi.getById(id);
      if (res) {
        values.ma = res.ma;
        values.ten = res.ten;
      } // eslint-disable-next-line
    } catch (err) {}
    setIsLoading(false);
  };

  const handleEditOnClick = async function (e) {
    e.preventDefault();
    try {
      if (isUpdatePage) {
        const res = await typeTeachingPlanApi.putById(id, values);
        if (res) {
          setNotification({
            color: "success",
            isOpen: true,
            messages: [t("admin.UPDATE_SUCCESS")],
          });
          updateForm(id);
        }else {
          toast.error(t("RESPONSE_MESSAGE500"));
        }
      } else {
        const res = await typeTeachingPlanApi.post(values);
        if (res) {
          toast.success(t("admin.ADD_SUCCESS"));
        } else {
          toast.error(t("RESPONSE_MESSAGE500"));
        }
      }
    } catch (err) {
      if (err.response) {
        if (err.response.status === 403 || err.response.status === 401) {
          toast.error(t("RESPONSE_MESSAGE401"));
        } else if (err.response.status === 400) {
          toast.error(t("RESPONSE_MESSAGE400"));
        } else if (err.response.status === 409) {
          toast.error(t("RESPONSE_MESSAGE409"));
        } else if (err.response.status === 404) {
          toast.error(t("RESPONSE_MESSAGE404"));
        } else {
          toast.error(t("RESPONSE_MESSAGE500"));
        }
      }
    }
  };

  const handleRefreshOnClick = function (e) {
    e.preventDefault();
    setNotification({
      color: "success",
      isOpen: false,
      messages: [],
    });
    resetForm();
  };

  const handleDeleteOnClick = async function (e) {
    e.preventDefault();
    setNotification({
      color: "success",
      isOpen: false,
      messages: [],
    });
    try {
      const res = await typeTeachingPlanApi.deleteById(id);
      if (res) {
        if (res.status === 200) {
          toast.success(t("admin.common.DELETED"));
          history.push(RoutesString.ADMIN_TYPE_TEACHING_PLAN_LIST)
        } else {
          toast.error(t("DELETE_ERROR")+": "+t("RESPONSE_MESSAGE500"));
        }
      }
    } catch (err) {
      if (err.response) {
        if (err.response.status === 403 || err.response.status === 401) {
          toast.error(t("DELETE_ERROR")+": "+t("RESPONSE_MESSAGE401"));
        } else if (err.response.status === 400) {
          toast.error(t("DELETE_ERROR")+": "+t("RESPONSE_MESSAGE400"));
        } else if (err.response.status === 404) {
          toast.error(t("DELETE_ERROR")+": "+t("RESPONSE_MESSAGE404"));
        } else {
          toast.error(t("DELETE_ERROR")+": "+t("RESPONSE_MESSAGE500"));
        }
      }
    }
  };

  return (
    <div className="page">
      {isLoading && <LoadingBar />}

      <Breadcrumbs
        currentItem={
          isUpdatePage
            ? t("admin.type_teaching_plan_edit.UPDATE")
            : t("admin.type_teaching_plan_edit.ADD")
        }
        items={[
          {
            href: RoutesString.ADMIN_TYPE_TEACHING_PLAN_LIST,
            title: t("admin.type_teaching_plan_list.LIST"),
          },
        ]}
      />

      <PageHeader
        headerTitle={
          isUpdatePage
            ? t("admin.type_teaching_plan_edit.UPDATE")
            : t("admin.type_teaching_plan_edit.ADD")
        }
      />

      <div className="page-content">
        <Form>
          <Notification
            color={notification.color}
            isOpen={notification.isOpen}
            messages={notification.messages}
          />
          <FormGroup className="row">
            <Label for="value" className="col-lg-3">
              {t("admin.type_teaching_plan_list.CODE")} <AsteriskIcon />
            </Label>
            <div className="col-lg-6">
              <InputGroup>
                <InputGroupAddon addonType="prepend">
                  <InputGroupText>
                    <FaUserAlt />
                  </InputGroupText>
                </InputGroupAddon>
                <Input
                  type="text"
                  name="ma"
                  placeholder={t(
                    "admin.type_teaching_plan_edit.CODE_PLACEHOLDER"
                  )}
                  value={values.ma}
                  onChange={handleChange}
                  invalid={errors.ma}
                />
              </InputGroup>
              {errors.ma && <FormText color="danger">{errors.ma}</FormText>}
            </div>
          </FormGroup>{" "}
          <br />
          <FormGroup className="row">
            <Label for="name" className="col-lg-3">
              {t("admin.type_teaching_plan_list.NAME")} <AsteriskIcon />
            </Label>
            <div className="col-lg-6">
              <InputGroup>
                <InputGroupAddon addonType="prepend">
                  <InputGroupText>
                    <FaTextWidth />
                  </InputGroupText>
                </InputGroupAddon>
                <Input
                  type="text"
                  name="ten"
                  placeholder={t(
                    "admin.type_teaching_plan_edit.NAME_PLACEHOLDER"
                  )}
                  value={values.ten}
                  onChange={handleChange}
                  invalid={errors.ten}
                />
              </InputGroup>
              {errors.ten && <FormText color="danger">{errors.ten}</FormText>}
            </div>
          </FormGroup>{" "}
          
          <br />
          <FormGroup>
            <Button color="success" onClick={handleEditOnClick}>
              {isUpdatePage ? (
                <>
                  <FaExchangeAlt /> {t("admin.common.UPDATE")}
                </>
              ) : (
                <>
                  <FaPlus /> {t("admin.common.ADD")}
                </>
              )}
            </Button>
            &emsp;
            {isUpdatePage && (
              <>
                <Button onClick={handleRefreshOnClick}>
                  <FaSync /> {t("admin.common.REFRESH")}
                </Button>
                &emsp;
                <Button color="danger" onClick={handleDeleteOnClick}>
                  <FaTrash /> {t("admin.common.DELETE")}
                </Button>
              </>
            )}
          </FormGroup>
        </Form>
      </div>
    </div>
  );
};

const TypeTeachingPlanEditFormikForm = withFormik({
  mapPropsToValues() {
    return {...initValues};
  },
  enableReinitialize: true,
  validationSchema: Yup.object().shape({
    ma: Yup.string().required(i18n.t("REQUIRED_FIELD_WARNING")),
    ten: Yup.string().required(i18n.t("REQUIRED_FIELD_WARNING")),
  }),
})(TypeTeachingPlanEdit);

export default TypeTeachingPlanEditFormikForm;
